body {
    background-color: rgb(238, 238, 238);
    padding: 0;
    margin: 0;
  }
  
  #con {
    display: flex;
    flex-direction: row;
    background-color: white;
    padding: 10px 5%; 
    border: none;
    border-radius: 10px;
  }
  
  .nav {
    display: flex;
    flex-direction: row;
    margin-right: 30px;
  }
  
  .containesr {
    display: flex;
    justify-content: center;
    margin-bottom: 300px;
  }
  
  #name_ {
    font-size: 25px;
    font-weight: 900;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    margin-right: 60px;
    padding: 10px 20px;
    font-family: "Playwrite HR", cursive;
  }
  
  .nav button, .btn {
    background-color: aqua;
    border: none;
    vertical-align: middle;
    padding: 20px 30px;
    border-radius: 30px;
    font-weight: 700;
  }
  
  .nav button:hover, .btn:hover {
    background-color: rgb(136, 29, 136);
    cursor: pointer;
    transition: background-color 0.5s;
  }
  
  .containesr3 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
    justify-content: space-between;
    margin-left: 16px;
  }
  
  .des {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 40px;
    width: 100%; 
    max-width: 600px; 
    color: white;
  }
  
  .btn {
    margin-right: 16px;
  }
  
  @media (max-width: 550px) {
    .des {
      font-size: 25px ;
      width: 400px; 
    }
    .w_i {
      width: 300px; 
      border-radius: 6px ;
      margin-right: 12px;
    }
  }
  